import {DNLEmbed} from './DNLEmbed.js';

//Webcomponent creating youtube iframe
export class DNLYoutubeEmbed extends DNLEmbed{
    static get observedAttributes(){
        return ["src"];
    }
    
    constructor(){
        super();
    }

    setupComponent(){
        const {
            shadowRoot
        } = this;
        let style = document.createElement("style");
        style.textContent = `
            :host{
                align-items: center;
                display: flex;
                justify-content: center;
                margin: 0 auto;
                max-width: 800px;
                width: 100%
            }            
            .wrapper{
                padding-bottom: 56.25%;
                position: relative;
                width: 100%;
            }            
            iframe{
                height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                width: 100%;
            }`
        
        shadowRoot.appendChild(style);

        const wrapper = document.createElement("div");
        wrapper.classList.add("wrapper");

        this.iframe = document.createElement("iframe");
        this.iframe.setAttribute("frameborder", "0");
        this.iframe.setAttribute("allow", "accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture");
        wrapper.appendChild(this.iframe);

        shadowRoot.appendChild(wrapper);
        this.polyfill();
    }

    updateComponent(){
        const src = this.getAttribute("src");
        if(!src) {
            console.error("No youtube-source found");
            return;
        }
        this.iframe.src = src;
    }

    resize(postedMessage){
        //Resized by CSS
    }
}
